import React, { Component } from 'react';
import './Styles/nutritionTable.css';

export default class NutritionTable extends Component {
  render() {
    return (
      <div
        className='nutritionTableInfoDetail'
        style={{
          backgroundColor: window.location.hash.includes('#MealViewer')
            ? 'var(--background-kinda-white)'
            : 'var(--background-white)',
        }}
      >
        <div className='nutritionTableInfoTitles'>
          {this.props.kcal ? <p className='nutritionTableInfoMain'> </p> : ''}
          <p className='nutritionTableInfoMain'></p>
          <p className='nutritionTableInfoMain'></p>
          <p className='nutritionTableInfoSub'></p>
          <p className='nutritionTableInfoSub'></p>
          <p className='nutritionTableInfoMain'></p>
          <p className='nutritionTableInfoSub'> </p>
          <p className='nutritionTableInfoSub'>  </p>
          <p className='nutritionTableInfoMain'></p>
          <p className='nutritionTableInfoSub'></p>
          <p className='nutritionTableInfoSub'></p>
          <p className='nutritionTableInfoSub'></p>
        </div>
        <div className='nutritionTableInfoValues'>
          {this.props.kcal ? <p className='nutritionTableInfoMain'>{this.props.kcal}</p> : ''}
          <p className='nutritionTableInfoMain'>{this.props.protein} </p>
          <p className='nutritionTableInfoMain'>{this.props.carb} </p>
          <p className='nutritionTableInfoSub'>{this.props.fiber} </p>
          <p className='nutritionTableInfoSub'>{this.props.sugar} </p>
          <p className='nutritionTableInfoMain'>{this.props.fat} </p>
          <p className='nutritionTableInfoSub'>{this.props.saturatedFat} </p>
          <p className='nutritionTableInfoSub'>{this.props.unsaturatedFat} </p>
          <p className='nutritionTableInfoMain' style={{ color: 'rgb(0 ,0 ,0 ,0)' }}>
            ......
          </p>
          <p className='nutritionTableInfoSub'>{this.props.cholesterol} </p>
          <p className='nutritionTableInfoSub'>{this.props.sodium} </p>
          <p className='nutritionTableInfoSub'>{this.props.potassium} </p>
        </div>
      </div>
    );
  }
}
